@import "./variables.scss";

nav {
    align-items: center;
    align-content: center;
    display: block;
}

.mi-header {
    position: fixed;
    align-items: center;
    left: 0px;
    top: 0px;
    transform: translateX(0px);
    height: 100vh;
    width: 300px;
    z-index: 10;
    background: rgb(25, 29, 43);
    border-right: 1px solid rgb(46, 52, 78);
    transition: all 0.4s ease-out 0s;

    @media screen and (max-width: 600px) {
        width: 0;
    }
}

.mi-header-inner {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden auto;
}

.mi-header-image {
    display: block;
    width: 100%;
    text-align: center;
    padding: 20px 0px;
    border-bottom: 1px solid rgb(46, 52, 78);
}

.mi-header-image a {
    height: 200px;
    width: 200px;
    display: inline-block;
    vertical-align: middle;
    border-radius: 1000px;
    border-width: 7px;
    border-style: solid;
    border-color: rgb(46, 52, 78);
    border-image: initial;
    overflow: hidden;
}

.mi-header-image img {
    max-width: 100%;
    vertical-align:super;
    border-style: none;
}

.mi-header-menu {
    width: 100%;
    padding-left: 0%;
    margin-top: 0px;
    margin-bottom: 1rem;
    display: block;
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}

.mi-header-menu li {
    text-align: center;
    display: block;
    list-style: none;
}

.mi-header-menu li a {
    padding: 20px 0px;
    text-transform: uppercase;
    margin: 1.2rem;
    font-size: 0.94rem;
    background-color: $bg-container-page;
    letter-spacing: 1px;
    display: block;
    color: rgb(164, 172, 196);
    font-weight: 600;
    position: relative;
    border-bottom-width: 4px;
    box-shadow: 0 0 0 0 1px rgba(#fff, .15) inset;
    border: 2px solid darken($bg-container-page,10);
    transition: all 0.4s ease-out 0s;
    overflow: hidden;
    text-shadow: 0 -1px 1px rgba(#000, .5);
    text-decoration: none;
    box-sizing: border-box; 
    &:before {
        content: "";
        display: block;
        width: 0;
        left: 50%;
        position: absolute;
        background-color: rgb(66, 73, 92);
        bottom: 0;
        transition: all .3s;
        height: 3px;
    }
    &:hover::before{
       width: 100%;
       left: 0;
    }
}

.mi-header span {
    display: inline-block;
}